import React, { Component } from 'react';
import Table from './component/Table';
import SVGMap from './component/SVGMap';

import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      size: {
        width: window.innerWidth,
        height: window.innerHeight
      },
      page: 'table',
      selected: null,
      radius: 5000,
      toggled: [],
      sort: 'desc',
      features: props.data.features
    };
/*
    this.sortFeatures = this.sortFeatures.bind(this);
    */
    this.changeRadius = this.changeRadius.bind(this);
    this.onFeatureToggle = this.onFeatureToggle.bind(this);
    this.onFeatureSelect = this.onFeatureSelect.bind(this);
    this.toggleSort = this.toggleSort.bind(this);

  }

  componentDidMount() {/*
    this.sortFeatures('radius5000');
    */
    window.addEventListener('resize', () => {
      this.setState({
        size: {
          width: window.innerWidth,
          height: window.innerHeight
        }
      });
    });
  }

  toggleSort() {
    this.setState(prevState => ({
      sort: prevState.sort === 'desc' ? 'asc' : 'desc'
    }));
  }

  changeRadius(radius) {
    this.setState({
      radius: radius
    });/*
    this.sortFeatures();
    */
  }

  onFeatureSelect(feature) {
    this.setState(prevState => ({
      selected: (prevState.selected !== null && prevState.selected.properties.id === feature.properties.id) ? null : feature
    }));
  }

  onFeatureToggle(feature) {
    const id = feature.properties.id;
    this.setState(prevState => ({
      toggled: prevState.toggled.indexOf(id) < 0 ? [...prevState.toggled, id] : prevState.toggled.filter(d => d !== id)
    }));
  }

  render() {
    return (
      <div className="app">
        <header>
          <div
            onClick={() => {
              this.setState({ page: 'table' });
            }}
            className={this.state.page === 'table' ? 'menu menu-active' : 'menu'}
          >
            {'TABLE'}
          </div>
          <div
            onClick={() => {
              this.setState({ page: 'map' });
            }}
            className={this.state.page === 'map' ? 'menu menu-active' : 'menu'}
          >
            {'MAP'}
          </div>
        </header>
        <div className="content" hidden={this.state.page !== 'table'}>
          <Table onFeatureSelect={this.onFeatureSelect}
           onFeatureToggle={this.onFeatureToggle}
           toggleSort={this.toggleSort}
           state={this.state}
           features={this.state.features} onChangeRadius={this.changeRadius} selected={this.state.selected} />
        </div>
        <div className="content" hidden={this.state.page !== 'map'}>
          <SVGMap selected={this.state.selected} state={this.state}/>
        </div>
      </div>
    );
  }
}

export default App;
