import React from 'react';
import { format as d3format } from 'd3-format';

import './RadiusTable.css';

const format = d3format(',');

export const distances = [
  {label: '500m', value: 500},
  {label: '1.0km', value: 1000},
  {label: '1.5km', value: 1500},
  {label: '3.0km', value: 3000},
  {label: '5.0km', value: 5000}
];

const RadiusTable = ({feature, radius}) => {
  const data = distances.map(d => ({
    label: `${d.label}圏`, radius: d.value, value: feature.properties[`radius${d.value}`]
  }));
  return (
    <table className="data-table">
      <thead>
        <tr>
          {
            data.map((d, i) => <td key={i} className={d.radius === radius ? 'thead-td-selected' : 'thead-td'}>{d.label}</td>)
          }
        </tr>
      </thead>
      <tbody>
        <tr>
          {
            data.map((d, i) => <td key={i}>{format(d.value)}</td>)
          }
        </tr>
      </tbody>
    </table>
  );
}

export default RadiusTable;
