import React, { Component } from 'react';

import Row from './Row';
import './Table.css';

class Table extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const sortValue = this.props.state.sort === 'desc' ? -1 : 1;
    const sortKey = `radius${this.props.state.radius}`;
    const features = this.props.state.features.sort(
      (a, b) => sortValue * (a.properties[sortKey] - b.properties[sortKey])
    );
    const distances = [
      { label: '500m', value: 500 },
      { label: '1.0km', value: 1000 },
      { label: '1.5km', value: 1500 },
      { label: '3.0km', value: 3000 },
      { label: '5.0km', value: 5000 }
    ];
    return (
      <div>
        <div className="table-header">
          <h2>
            {`スタジアムから${
              distances[
                distances.map(d => d.value).indexOf(this.props.state.radius)
              ].label
            }圏内の人口`}
          </h2>
          <div>
            <ul>
            {distances.map((d, i) => (
              <li key={i}>
              <a
                href="#"
                onClick={() => {
                  this.props.onChangeRadius(d.value);
                }}
              >
                {d.label}
              </a>
              </li>
            ))}
            <li>
              <a href="#" onClick={() => {
                this.props.toggleSort();
              }}>
                {this.props.state.sort === 'asc' ? '昇順' : '降順'}
              </a>
            </li>
            </ul>
          </div>
        </div>
        <div>
          {features.map((feature, i) => (
            <Row
              key={i}
              feature={feature}
              onFeatureSelect={this.props.onFeatureSelect}
              onFeatureToggle={this.props.onFeatureToggle}
              selected={
                this.props.selected !== null &&
                this.props.selected.properties.id === feature.properties.id
              }
              index={i}
              toggled={
                this.props.state.toggled.indexOf(feature.properties.id) >= 0
              }
              radius={this.props.state.radius}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default Table;
