import React, { Component } from 'react';
import { format as d3format } from 'd3-format';

import RadiusTable from './RadiusTable';
import './Row.css';

class Row extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const props = this.props;
    const feature = props.feature;
    const { name, club, category } = feature.properties;
    const format = d3format(',');
    return (
      <div className={this.props.selected ? 'row row-selected' : 'row'}>
        <div className="row-head">
          <div className="grid row-select-button">
            <input
              type="radio"
              checked={this.props.selected}
              onChange={() => {
                this.props.onFeatureSelect(feature);
              }}
            />
          </div>
          <div className="grid row-index">
            <span>{this.props.index + 1}</span>
          </div>
          <div
            className="grid row-name"
            onClick={() => {
              this.props.onFeatureToggle(feature);
            }}
          >
            <span>{name}</span>
          </div>
          <div className="grid row-value">
            <span>
              {format(feature.properties[`radius${this.props.radius}`]) + '人'}
            </span>
          </div>
        </div>
        <div className="row-dataField" hidden={!this.props.toggled}>
          <div>
            {club === 'なし' ? null : club.split('&').length > 1 ? (
              <ul>
                {club
                  .split('&')
                  .map((cl, i) => (
                    <li key={i}>{`${cl}(${category.split('&')[i]})`}</li>
                  ))}
              </ul>
            ) : (
              <p>{`${club}(${category})`}</p>
            )}
            <RadiusTable radius={this.props.radius} feature={feature} />
          </div>
        </div>
      </div>
    );
  }
}
export default Row;
